import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import ModalCommon from "../../common/Modal/Modal";
import { useDispatch, useSelector } from "react-redux";
import { Wrapper } from "./styled-index";
import {
  RaitingGet,
  RaitingPost,
  UploadImage,
  UploadImagePdf,
} from "../../../redux/raiting";
function BankAddAboutForm({ Open, HandleClose, AboutId }) {
  const dispatch = useDispatch();
  const prognozRef = useRef();
  const [typeReting , typeRetingSet] = useState();
  const update_dateRef = useRef();
  const RaitingRef = useRef();
  const typeRetingRef = useRef();
  const typeRetingLinkRef = useRef();
  const pressRelis = useRef();
  const dataClient = useSelector((state) => state.banks.uploadImages);
  const dataClient2 = useSelector((state) => state.ratingabout.UploadimagePdf);
  const HandleChange = async (e) => {
    await dispatch(UploadImage(e));
  };
  const HandleChange2 = async (e) => {
    await dispatch(UploadImagePdf(e));
  };
  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      raiting: RaitingRef.current.value,
      bank_id: AboutId,
      prognoz: prognozRef.current.value,
      update_date: update_dateRef.current.value,
      type_reting: typeRetingRef.current.value,
      sertifikat: dataClient.data,
      link: typeReting,
      update_date_pdf: dataClient2.data,
    };
    console.log(body);
    await dispatch(RaitingPost(body));
    dispatch(RaitingGet());
    HandleClose();
  };
  return (
    <ModalCommon height={370} width={400} open={Open} handleClose={HandleClose}>
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <h3>информация о рейтинге</h3>
          {dataClient.Loading == true ? (
            <span className="loading">loading...</span>
          ) : (
            <>
              <input type="file" id="file" onChange={HandleChange} />
              <label for="file" class="custom-file-upload">
                <span className="span-download">
                  <ion-icon name="cloud-download-outline"></ion-icon>
                </span>
                загрузить изображение
              </label>
            </>
          )}
          {dataClient2?.Loading == true ? (
            <span className="loading">loading...</span>
          ) : (
            <>
              <input type="file" id="filepdf" onChange={HandleChange2} />
              <label for="filepdf" class="custom-file-upload">
                <span className="span-download">
                  <ion-icon name="cloud-download-outline"></ion-icon>
                </span>
                загрузить пдф
              </label>
            </>
          )}
          <input type="text" placeholder="рейтинг" required ref={RaitingRef} />
          <input
            type="text"
            placeholder="прогноз"
            required
            ref={update_dateRef}
          />
          <input
            type="text"
            placeholder="дата обновления"
            required
            ref={prognozRef}
          />
          <input
            type="text"
            placeholder="вид рейтинга"
            required
            ref={typeRetingRef}
          />

          <select  onChange={(e) => typeRetingSet(e.target.value)}  name="" id="">
            <option value="1">Кредитоспособности и надежности</option>
            <option value="2">Платежеспособности и надежности</option>
            <option value="3">Эмитента ценных бумаг</option>
            <option value="4">Экспресс - рейтинг</option>
          </select>
          <CommonBtn type={"submit"} style={{ marginTop: "20px" }}>
            Добавить
          </CommonBtn>
        </Wrapper>
      </>
    </ModalCommon>
  );
}

export default BankAddAboutForm;
